@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;500;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  letter-spacing: 1px;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #d8d7d7;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  min-height: 100vh;
  transition: background 0.2s linear;
}

body.dark {background-color: #292c35;} /* #9b59b6 */

body.dark h1, body.dark .support a {color: #fff;}

.checkbox {
  opacity: 0;
  position: absolute;
}

.checkbox-label {
  border: 1px solid #fff;
  background-color: #111;
  width: 50px;
  height: 26px;
  border-radius: 50px;
  position: relative;
  padding: 5px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fa-moon {color: #f1c40f;}

.fa-sun {color: #f39c12;}

.checkbox-label .ball {
  background-color: #fff;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 2px;
  top: 2px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

.checkbox:checked + .checkbox-label .ball {
  transform: translateX(24px);
}




/*  Support me if you like it */
.support {
	position: absolute;
	right: 20px;
	bottom: 20px;
}

.support a {
  color: #292c35;
  font-size: 32px;
  backface-visibility: hidden;
  display: inline-block;
  transition: transform 0.2s ease;
}

.support a:hover{
  transform: scale(1.1);
}





.Calculator{
  background-color: #fff;
  width: 300px;
  min-width: 300px;
  margin: 0 auto 0 ;
  border-radius: 28px;
}

.Calculator nav{
  padding: 14px ;
  background-color: #121212;
  color: #fff;
  display: grid;
  grid-template-columns: 1fr 10fr;
  border-radius: 28px 24px 0 0;
  border: none;
  margin: 0;
}

.Calculator nav h3{
  text-align: start;
  padding: 0 10px;
  align-self: center;
  border: none;
}

.Calculator .display{
  background-color: #121212;
  text-align: right;
  padding: 40px 20px;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  border-radius: 0 0 14px 14px;
  border: none;
  margin: 0;
}

.Calculator .display .typing-display{
  color: #a160fb;
  font-weight: bold;
  font-size: small;
}

.Calculator .display .result-display{
  color: #fff;
  font-size: xx-large;
  font-weight: bolder;
  padding: 1px;
}

.Calculator .keypad{
  padding: 20px 10px;
  display: grid;
  grid-template-columns: repeat(4, 70px);
  user-select: none;
}

.Calculator .keypad span{
  font-weight: bolder;
  font-size: larger;
  padding: 14px;
  margin: 9px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 12px;
  cursor: pointer;
  user-select: none;
}

.Calculator .keypad span:last-child{
  color: #fff;
  background-color: #a160fb;
}

.Calculator .keypad span:nth-child(1),
.Calculator .keypad span:nth-child(2),
.Calculator .keypad span:nth-child(3),
.Calculator .keypad span:nth-child(4),
.Calculator .keypad span:nth-child(8),
.Calculator .keypad span:nth-child(12),
.Calculator .keypad span:nth-child(16){
  color: rgb(139, 139, 139);
  font-weight:200;
}

.Calculator .keypad span:nth-child(4),
.Calculator .keypad span:nth-child(8),
.Calculator .keypad span:nth-child(12),
.Calculator .keypad span:nth-child(16),
.Calculator .keypad span:last-child{
  font-size: xx-large;
  padding: 5px;
}
.Calculator .keypad span:active{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.darktheme{
  background-color: #191919;
  color: #f1f1f1;
}
.darktheme .display{
  box-shadow: #000000 0px 2px 1px, #000000 0px 4px 2px, #191919 0px 8px 4px, #191919 0px 16px 8px, #191919 0px 64px 32px;
}
.darktheme .keypad span{
  box-shadow: rgb(0, 0, 0) 3px 5px 10px;
}

.darktheme .keypad span:active{
  box-shadow: rgba(0, 0, 0, 0.8) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.7) 0px 18px 36px -18px inset;
}